@media only screen and (max-width: 2000px) {
  .header {
    
    margin: 0 auto;
    width: 95vw;
    background-color: #fefefe;
    color: #282c34;
    font-family: "apercu", sans-serif;
  }

  .image img,
  .imageArea img,
  .img2 img,
  .imageOffice img,
  .imageGym img {
    width: 48%;
    margin: 1%;
    height: auto;
  }
  .title {
    font-family: "apercu", sans-serif;
    font-weight: bold;
    font-size: 50px;
    text-transform: uppercase;
    padding-left: 4%;
  }

  .subtitle {
    color: #c0c0c0;
    padding-left: 2%;
    text-transform: uppercase;
    font-size: 10px;
  }

  .text .printText {
    width: 60%;
    padding-left: 40%;
    padding-top: 30px;
  }

  /*начало цитаты*/
  .tagline {
    height: 100vh;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex-wrap: nowrap;
    justify-content: center;
  }

  .tagline div {
    margin: 0px;
    padding: 0px;
  }
  .tagline .slogan {
    font-family: "apercu", sans-serif;
    font-weight: 500;
    font-size: 50px;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
  }

  .slogan p {
    margin: 0px;
    padding: 0px;
  }
  .slogan .sl1 {
    width: 50%;
    text-align: end;
  }
  .slogan .sl2 {
    width: 50%;
    padding-left: 50%;
    text-align: start;
  }
  .tagline .subSlogan {
    color: #c0c0c0;
    text-align: end;
    padding-right: 5%;
  }
  /* конец цитаты*/
  .mainPage {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }

  .text {
    width: 50%;
  }
  .textAr {
    display: flex;
  }

  .textAr .areaTextTitle {
    width: 50%;
    font-family: "apercu", sans-serif;
    font-weight: bold;
    font-size: 50px;
    text-transform: uppercase;
    padding-left: 20px;
  }
  .textAr .areaText {
    width: 50%;
    padding-left: 20px;
  }
  .image {
    width: 50%;

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
  }
  .black {
    background-color: #282c34;
    color: #fefefe;
    font-family: "apercu", sans-serif;
    font-weight: bold;
    font-size: 36px;
    text-transform: uppercase;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
  }
}
/*font-size: calc(24px + 16 * ((100vw - 320px) / (1280 - 320)));*/
