@media only screen and (max-width: 992px) {
  .mainPage {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: nowrap;
    align-items: center;
  }

  .image {
    width: 100%;
    padding-top: 500px;
  }
  .tagline {
    padding-top: 150px;
  }
  .image img,
  .imageArea img,
  .img2 img,
  .imageOffice img,
  .imageGym img {
    width: 98%;
    margin: 1%;
    height: auto;
  }
  .tagline .slogan {
    font-size: 35px;
  }
}
@media only screen and (max-width: 768px) {
  .mainPage {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
  }
  .text {
    width: 100%;
  }
  .title {
    font-family: "apercu", sans-serif;
    font-weight: bold;
    font-size: 44px;
    text-transform: uppercase;
    padding-left: 0%;
    text-align: inherit;
  }
  .image {
    width: 100%;
    padding-top: 0px;
  }
  .tagline {
    height: 50vh;
    padding-top: 0px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    flex-wrap: nowrap;
    justify-content: center;
  }
  .tagline .slogan {
    font-size: 25px;
  }
  .textAr {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
  }
  .textAr .areaTextTitle {
    width: 100%;
    font-family: "apercu", sans-serif;
    font-weight: bold;
    font-size: 50px;
    text-transform: uppercase;
    padding-left: 0px;
  }
  .textAr .areaText {
    width: 100%;
    padding-left: 0px;
  }
}
@media only screen and (max-width: 480px) {
  .title {
    font-size: 35px;
  }
  .tagline .slogan {
    font-size: 17px;
  }
  .textAr .areaTextTitle {
    font-size: 40px;
  }
  .black {
    font-size: 28px;
  }
}
